body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: whitesmoke;
}

.App-header {
  color: black;
  padding: 12px 35px 0px 35px;
  text-align: center;
}

@media (min-width: 576px) {
  .App-header {
    text-align: right;
  }
}

footer {
  color: #6c757d;
  text-align: center;
  margin: 10px;
  font-size: 80%;
}

/* SHELF */
.shelf-header {
  /* top right bottom left */
  margin: 20px 0px 20px 10vh;
}

.shelf {
  padding: 40px;
  display: grid;
  grid-gap: 30px;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  grid-auto-rows: 400px;
}

@media (min-width: 576px) {
  .shelf {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  }
}

/* BOOK */
.book {
  max-width: 150px;
  justify-self: center;
}

.book img {
  /* max-width: 200px; */
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.book .title {
  margin-top: 15px;
  font-weight: bold;
}

.book .title a {
  color: inherit;
  text-decoration: none;
}

.book .title a:hover {
  text-decoration: underline;
}

/* BOOK-SHELF ICON */
@-webkit-keyframes book-bounce {
  0% { -webkit-transform: translateY(0); transform: translateY(0); }
  40% { -webkit-transform: translateY(-10px); transform: translateY(-10px); }
  80% { -webkit-transform: translateY(0); transform: translateY(0); }
  100% { -webkit-transform: translateY(0); transform: translateY(0); }
}
@keyframes book-bounce {
  0% { -webkit-transform: translateY(0); transform: translateY(0); }
  40% { -webkit-transform: translateY(-10px); transform: translateY(-10px); }
  80% { -webkit-transform: translateY(0); transform: translateY(0); }
  100% { -webkit-transform: translateY(0); transform: translateY(0); }
}

@-webkit-keyframes shelf-lift {
  0% { -webkit-transform: translateY(0) rotate(0); transform: translateY(0) rotate(0); }
  20% { -webkit-transform: translateY(-4px) rotate(10deg); transform: translateY(-4px) rotate(10deg); }
  40% { -webkit-transform: translateY(-4px) rotate(0); transform: translateY(-4px) rotate(0); }
  40% { -webkit-transform: translateY(-4px) rotate(-10deg); transform: translateY(-4px) rotate(-10deg); }
  80% { -webkit-transform: translateY(0); transform: translateY(0); }
  100% { -webkit-transform: translateY(0); transform: translateY(0); }
}

@keyframes shelf-lift {
  0% { -webkit-transform: translateY(0) rotate(0); transform: translateY(0) rotate(0); }
  20% { -webkit-transform: translateY(-4px) rotate(10deg); transform: translateY(-4px) rotate(10deg); }
  40% { -webkit-transform: translateY(-4px) rotate(0); transform: translateY(-4px) rotate(0); }
  40% { -webkit-transform: translateY(-4px) rotate(-10deg); transform: translateY(-4px) rotate(-10deg); }
  80% { -webkit-transform: translateY(0); transform: translateY(0); }
  100% { -webkit-transform: translateY(0); transform: translateY(0); }
}

.book-shelf {
  -webkit-animation: book-bounce 3600ms ease-in-out 6000s;
          animation: book-bounce 3600ms ease-in-out 6000s;
  -webkit-transform-origin: top;
          transform-origin: top;

  position: absolute;
  right: 9px;
  top: -4px;
}


/* Hover */
.book-shelf {
  opacity: 0.9;
  -webkit-transition: opacity 0.2s ease-in;
  transition: opacity 0.2s ease-in;
}

.book-shelf:hover {
  cursor: pointer;
  opacity: 1.0;
  -webkit-transition: opacity 0.2s ease-in;
  transition: opacity 0.2s ease-in;
}

.book-shelf.bounce .book-shelf__book {
  -webkit-animation: book-bounce 0.4s ease;
          animation: book-bounce 0.4s ease;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

.book-shelf.bounce .book-shelf__book--two {
  -webkit-animation-delay: 0.04s;
          animation-delay: 0.04s;
}

.book-shelf.bounce .book-shelf__book--three {
  -webkit-animation-delay: 0.10s;
          animation-delay: 0.10s;
}

.book-shelf.bounce .book-shelf__shelf {
  -webkit-animation: shelf-lift 0.4s ease;
          animation: shelf-lift 0.4s ease;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
}

/* Loading */
.book-shelf.loading {
  opacity: 0.7;
  -webkit-transition: opacity 0.2s ease-in;
  transition: opacity 0.2s ease-in;
  cursor: default;
}

.book-shelf.loading .book-shelf__book {
  -webkit-animation: book-bounce 0.4s ease;
          animation: book-bounce 0.4s ease;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.book-shelf.loading .book-shelf__book--two {
  -webkit-animation-delay: 0.04s;
          animation-delay: 0.04s;
}

.book-shelf.loading .book-shelf__book--three {
  -webkit-animation-delay: 0.10s;
          animation-delay: 0.10s;
}
